import { createStore } from 'redux'

const defaultState = {
	comments: [],
	question: null,
	processed: null,
	stage: 0,
	options: {
		song: null,
		theme: null,
	}
}

function state(state = defaultState, action) {
	switch (action.type) {
		case 'SET_STAGE':
			return Object.assign({}, state, {
				stage: action.val,
			})
		case 'INC_STAGE':
			return Object.assign({}, state, {
				stage: state.stage + action.val,
			})
		case 'SET_COMMENTS':
			return Object.assign({}, state, {
				comments: action.val,
			})
		case 'SET_PROCESSED':
			return Object.assign({}, state, {
				processed: action.val,
			})
		case 'SET_QUESTION':
			return Object.assign({}, state, {
				question: action.val,
			})
		case 'SET_TITLE':
			let question = Object.assign({}, state.question, { title: action.val })
			return Object.assign({}, state, {
				question: question,
			})
		case 'SET_OPTIONS':
			return Object.assign({}, state, {
				options: action.val,
			})
		default:
			return state
	}
}

export default createStore(state)