import React from 'react'
import { getThread } from './api'
import { connect } from 'react-redux'
import { setComments, setQuestion, nextStage } from './redux/actions'

class Thread extends React.Component {
	constructor(props) {
		super(props)
		this.state = {
			input: '',
			loading: null,
			error: null,
		}
		this.fetchThread = this.fetchThread.bind(this)
		this.onSubmit = this.onSubmit.bind(this)
		this.handle = this.handle.bind(this)
	}
	fetchThread(thread) {
		this.setState({ error: null, loading: true })

		getThread(thread)
			.then(json => {
				let mapping = comment => {
					let newcomment = Object.assign({}, comment)

					if (Array.isArray(newcomment.replies)) {
						newcomment.replies = newcomment.replies.map(mapping)
					}

					return {
						enabled: false,
						data: newcomment
					}
				}

				let comments = json[1].map(mapping)

				this.props.setQuestion(json[0])
				this.props.setComments(comments)
				this.props.nextStage()
			})
			.catch(err => {
				console.log(err)
				this.setState({ error: err.message, loading: false })
			})
	}
	onSubmit(e) {
		e.preventDefault()

		let thread = this.state.input

		if (thread.indexOf('comments') !== -1) {
			let pattern = /\/comments\/([\w]+)/
			let match = thread.match(pattern)

			if (match) {
				thread = match[1]
			}
		}

		let onlythread = thread.replace(/[^\w\d]/g, '')

		this.fetchThread(onlythread)
	}
	handle(e) {
		this.setState({ input: e.target.value })
	}
	render() {
		return (
			<div className="Thread">
				<h1>Choose a thread</h1>
				<form onSubmit={this.onSubmit}>
					<input value={this.state.input} onChange={this.handle} placeholder="Put thread ID or url" autoFocus />
					<button>OK</button>
				</form>
				<div>
					{this.state.loading && <p className="loader">Loading...</p>}
					{this.state.error && <p style={{ color: 'red' }}>{this.state.error}</p>}
				</div>
			</div>
		)
	}
}

export default connect(null, {
	setComments,
	setQuestion,
	nextStage,
})(Thread);