let apiurl = process.env.NODE_ENV ? 'http://localhost:5566/api' : '/api' // use current web page as host

export let getThread = function (thread) {
	return fetch(apiurl + '/get-thread/' + thread)
		.then(r => {
			if (r.ok) {
				return r.json()
			} else {
				return r.json()
					.then(j => {
						throw j
					})
			}
		})
}

export let getSongs = function () {
	return fetch(apiurl + '/get-songs')
		.then(r => r.json())
}

export let getThemes = function () {
	return fetch(apiurl + '/get-themes')
		.then(r => r.json())
}

export let postRender = function (questionData, commentData, options) {
	return fetch(apiurl + '/render-video', {
		method: 'POST',
		body: JSON.stringify({
			questionData,
			commentData,
			options,
		})
	})
		.then(r => r.json())
}