import React from 'react';
import PickComments from './PickComments/index';
import Question from './Question';
import SelectThread from './SelectThread';
import Settings from './Settings';
import { connect } from 'react-redux'
import { prevStage, nextStage } from './redux/actions'
import './App.css';
import 'draft-js-inline-toolbar-plugin/lib/plugin.css'

class App extends React.Component {
	render() {
		return <div className="App">
			{this.props.stage === 0 ?
				<SelectThread /> :
				this.props.stage === 1 ?
					<Question /> :
					this.props.stage === 2 ?
						<PickComments /> :
						this.props.stage === 3 ?
							<Settings /> :
							this.props.stage === 4 ?
								<div>
									<h1>rendering atm bro</h1>
									<p>You may close this tab</p>
								</div> :
								null
			}
			<br />
			{this.props.stage !== 0 &&
				<button style={{ fontSize: 12 }} onClick={this.props.prevStage}>GO BACK!</button>
			}
		</div>
	}
}

let mapStateToProps = store => {
	return {
		question: store.question,
		processed: store.processed,
		stage: store.stage,
		options: store.options,
	}
}

export default connect(mapStateToProps, {


	prevStage,
	nextStage,

})(App);