export const setProcessed = content => ({
	type: 'SET_PROCESSED',
	val: content,
})
export const setQuestion = content => ({
	type: 'SET_QUESTION',
	val: content,
})
export const setStage = content => ({
	type: 'SET_STAGE',
	val: content,
})
export const nextStage = () => ({
	type: 'INC_STAGE',
	val: 1,
})
export const prevStage = () => ({
	type: 'INC_STAGE',
	val: -1,
})
export const setComments = content => ({
	type: 'SET_COMMENTS',
	val: content,
})
export const setOptions = content => ({
	type: 'SET_OPTIONS',
	val: content,
})
export const setTitle = content => ({
	type: 'SET_TITLE',
	val: content,
})