import React from 'react'

import './FakeComment.css'

class FakeComment extends React.Component {
	render() {
		let { comment } = this.props
		return (
			<div className="FakeComment">
				<div dangerouslySetInnerHTML={{ __html: comment.data.body_html }}>
				</div>
				{comment.data.replies &&
					comment.data.replies.filter &&
					comment.data.replies
						.filter(r => r.enabled)
						.map(rep => <FakeComment key={rep.data.id} comment={rep} />)}
			</div>
		)
	}
}

export default FakeComment;