import React from 'react';
import { connect } from 'react-redux';
import { setTitle, nextStage } from './redux/actions';

class PickComments extends React.Component {
	onSubmit(e) {
		e.preventDefault()
		this.props.nextStage()
	}
	onChange(e) {
		this.props.setTitle(e.target.value)
	}
	render() {
		return (
			<div className="Question">
				<h1>Edit question</h1>
				<form onSubmit={this.onSubmit.bind(this)}>
					<textarea defaultValue={this.props.title} className="question-title" onChange={this.onChange.bind(this)} style={{ width: 500, fontFamily: 'Noto Sans' }} />
					<br />
					<button type="submit">Submit</button>
				</form>
			</div>
		)
	}
}

export default connect(state => ({
	title: state.question.title,
}), { setTitle, nextStage })(PickComments);