import React from 'react'
import { connect } from 'react-redux'
import { getThemes, postRender } from './api';
import { setOptions, nextStage } from './redux/actions'

class Settings extends React.Component {
	constructor(props) {
		super(props)

		this.state = { themes: [] }

		getThemes()
			.then(themes => {
				this.setState({
					themes,
				})
			})

		this.onSubmit = this.onSubmit.bind(this)
		this.changeSong = this.changeSong.bind(this)
		this.changeTheme = this.changeTheme.bind(this)
	}
	onSubmit(e) {
		e.preventDefault()

		postRender(this.props.question, this.props.processed, this.props.options)

		this.props.nextStage()
	}
	changeSong(e) {
		let newOptions = {
			song: e.target.value,
			theme: this.props.options.theme,
		}
		this.props.setOptions(newOptions)
	}
	changeTheme(e) {
		let newOptions = {
			song: null,
			theme: e.target.value,
		}
		this.props.setOptions(newOptions)
	}
	render() {
		let chosenTheme = this.state.themes.find(t => t.name === this.props.options.theme)

		return (
			<div className="Thread">
				<form onSubmit={this.onSubmit}>
					<div>
						<select value={this.props.options.theme || ''} style={{ display: 'block', margin: '0 auto' }} onChange={this.changeTheme}>
							<option value={''}>Select a theme</option>
							{this.state.themes.map(theme => <option value={theme.name} key={theme.name}>{theme.name}</option>)}
						</select>
						<select value={this.props.options.song || ''} disabled={!this.props.options.theme} style={{ display: 'block', margin: '10px auto' }} onChange={this.changeSong}>
							<option value={''}>Select a song</option>
							{chosenTheme &&
								chosenTheme.songs.map(song => <option value={song} key={song}>{song}</option>)
							}
						</select>
					</div>
					<div>
						<button type="submit" disabled={!this.props.options.theme || !this.props.options.song}>Send to the slaughterhouse</button>
					</div>
				</form>
			</div>
		)
	}
}

export default connect(state => ({
	question: state.question,
	processed: state.processed,
	options: state.options,
}), { setOptions, nextStage })(Settings);